//=========Media Queries============
$telefono: 480px;
$tabletChica: 576px;
$tablet: 769px;
$desktop: 1200px;

//========Colores===============
$azul: #75d3ff;
$negro: #0a0808;
$amarillo: #E08709;;
$verde: #71B100;
$blanco: #FFFFFF;
$gris: #e1e1e1;
$grisOscuro: #333333;
$rojo: #ff433c;

//========Font Size========
$delgada: 300;
$regular : 400;
$bold: 700;
$black: 900;

/*========== BASE ==========*/
*,::before,::after{
    box-sizing: border-box;
}

/*===== VARIABLES CSS =====*/
:root{
    --header-height: 3rem;

    /*========== Colors ==========*/
    --first-color: #c03535;
    --first-color-alt: #cc8585;
    --first-color-send: #b62c2c;
    --title-color: #0a0808;
    --text-color: #2c2727;
    --text-color-light: #ff9f9f;
    --body-color: #FEFBFB;
    --color-back-scroll: #ffdcdc;
    --container-color: #FFF;
    --nav-color: #ffffff ;
    --nav-productos-shadow: rgba(0,0,0,.1);
    --text-color-sign: #5d4949;

    /*========== Font and typography ==========*/
    --body-font: 'Poppins', sans-serif;

    --biggest-font-size: 2rem;
    --h2-font-size: 1.25rem;
    --h3-font-size: 1.125rem;
    --normal-font-size: .938rem;
    --small-font-size: .813rem;

    @media screen and (min-width: 768px){
        --normal-font-size: 1rem;
        --small-font-size: .875rem;
    }

    @media screen and (min-width: 968px){
        --biggest-font-size: 3rem;
        --h2-font-size: 1.75rem;
        --h3-font-size: 1.25rem;
        --normal-font-size: 1rem;
        --small-font-size: .875rem;
    }

    /*========== Font weight ==========*/
    --font-semi-bold: 600;
    --font-bold: 700;

    /*========== Margenes ==========*/
    --mb-1: .5rem;
    --mb-2: 1rem;
    --mb-3: 1.5rem;
    --mb-4: 2rem;
    --mb-5: 2.5rem;
    --mb-6: 3rem;

    /*========== z index ==========*/
    --z-tooltip: 10;
    --z-fixed: 100;
}

html{
    scroll-behavior: smooth;
}

/*========== Variables Dark theme ==========*/
body.dark-theme{
    --first-color-send: #e22f2f;
    --title-color: #F3F1F1;
    --text-color: #eeebdd;
    --body-color: #1b1717;
    --text-color-light: #ffe4e4; 
    --color-back-scroll: #200202;
    --container-color: #e24b4b;
    --nav-color: #1b1717 ;
    --nav-productos-shadow: rgba(255, 255, 255, 0.1);
}

/*========== Button Dark/Light ==========*/
.change-theme {
    position: absolute;
    right: 1.5rem;
    top: 2.2rem;
    display: flex;
    color: var(--title-color);
    font-size: 2rem;
    cursor: pointer;
}

body{
    margin: var(--header-height) 0 0 0;
    font-family: var(--body-font);
    font-size: var(--normal-font-size);
    background-color: var(--body-color);
    color: var(--text-color);
    line-height: 1.6;   
}
h1,h2,h3,ul,p{
    margin: 0;  
}

h1,h2,h3{
    font-weight: var(--font-semi-bold);
    color: var(--title-color);
}

ul{
    padding: 0;
    list-style: none;
}
a{
    text-decoration: none;
}
img{
    max-width: 100%;
    height: auto;
}

main {
    z-index: 900;
}


