/*===== LOGIN=====*/
.login{
    display: grid;
    grid-template-columns: 100%;
    height: 100vh;
    margin-left: 1.5rem;
    margin-right: 1.5rem;
    
    &__content{
        display: grid;
    }
    &__img{
        justify-self: center;
        & img{
            width: 310px;
            margin-top: 1.5rem;
        }
    }
    &__forms{
        position: relative;
        height: 368px;
    }
    &__registre, &__create{
        position: absolute; 
        bottom: 1rem;
        width: 100%;
        background-color: var(--container-color);
        padding: 2rem 1rem;
        border-radius: 1rem;
        text-align: center;
        box-shadow: 0 8px 20px rgba(35,0,77,.2);
        animation-duration: .4s;
        animation-name: animate-login;

        @keyframes animate-login {
            0%{ 
                transform: scale(1,1);
            }
            50%{ 
                transform: scale(1.1,1.1);
            }
            100%{ 
                transform: scale(1,1);
            }
        }
    }
    &__title{
        font-size: var(--h1-font-size);
        margin-bottom: 2rem;
    }
    &__box{
        display: grid;
        grid-template-columns: max-content 1fr;
        column-gap: .5rem;
        padding: 1.125rem 1rem;
        background-color: #FFF;
        margin-top: 1rem;
        border-radius: .5rem;
    }
    &__icon{
        font-size: 1.5rem;
        color: var(--first-color);
    }
    &__input{
        border: none;
        outline: none;
        font-size: var(--normal-font-size);
        font-weight: 700;
        color: var(--text-color-sign);

        &::placeholder{
            font-size: var(--normal-font-size);
            font-family: var(--body-font);
            color: var(--text-color-sign);
        }
    }
    &__forgot{
        display: block;
        width: max-content;
        margin-left: auto;
        margin-top: .5rem;
        font-size: var(--small-font-size);
        font-weight: 600;
        color: var(--first-color-light);
    }
    &__button {
        display: block;
        padding: 1rem;
        margin: 2rem 0;
        background-color: var(--first-color);
        color: #FFF;
        font-weight: 600;
        text-align: center;
        border-radius: .5rem;
        transition: .3s;
        cursor: pointer;
        

        &:hover{
            background-color: var(--first-color-send);
        }
    }
    &__account, &__signin, &__signup{
        font-weight: 600;
        font-size: var(--small-font-size);
    }
    &__account{
        color: var(--first-color-dark);
    }
    &__signin, &__signup{
        color: var(--first-color);
        cursor: pointer;
    }
    &__social{
        margin-top: 2rem;

        &-icon{
            font-size: 1.5rem;
            color: var(--first-color-dark);
            margin: 0 1.5rem;
        }
    }

    &__submit {
        input[type="submit"] {
            background-color: var(--first-color);
            width: 100%;
            height: 100%;
            padding: 1rem;
            margin: 2rem 0;
            border: none;
            border-radius: .5rem;
            color: #FFF;
            cursor: pointer;
        
            &:hover{
                background-color: var(--first-color-send);
            }
        }
    }
}

/*Show login*/
.block{
    display: block;
}

/*Hidden login*/
.none{
    display: none;
}

/* ===== MEDIA QUERIES =====*/

@media screen and (min-width: 576px){
    .login{
        &__forms{
            width: 348px;
            justify-self: center;
        }
    }
}

@media screen and (min-width: 1024px){
    .login{
        height: 100vh;
        overflow: hidden;

        &__content{
            grid-template-columns: repeat(2, max-content);
            justify-content: center;
            align-items: center;
            margin-left: 10rem;
        }
        &__img{
            display: flex;
            width: 600px;
            height: 588px;
            background-color: var(--first-color-lighten);
            border-radius: 1rem;
            padding-left: 1rem;
            
            & img{
                width: 390px;
                margin-top: 0;
            }
        }

        &__registre, &__create{
            left: -11rem;
        }
        &__registre{
            bottom: -2rem;
        }
        &__create{
            bottom: -5.5rem;
        }
    }
}


