.alerta {
    position: relative;
    color: $blanco;
    display: flex;
    height: 1rem;

    .texto {
        position: absolute;
        // left: 30px;
        top: 0;
        display: block;
        text-align: center;
        font-weight: $bold;
        text-transform: uppercase;
        padding: 0 .5rem;
        background-color: $rojo;

        &.error {
            top: 5rem;
            left: 0;
            right: 0;
            margin: 0 auto;
            background-color: $rojo;
        }
    
        &.exito {
            top: 5rem;
            left: 0;
            right: 0;
            margin: 0 auto;
            background-color: $verde;
        }

    }    

    
}

