/*========== LAYOUT ==========*/
.l-main {
  overflow: hidden; }

.bd-container {
  max-width: 968px;
  width: calc(100% - 3rem);
  margin-left: var(--mb-3);
  margin-right: var(--mb-3); }

.bd-grid {
  display: grid;
  gap: 1.5rem; }

.l-header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
