/*========== CLASS CSS ==========*/
.section {
    padding: 4rem 0 2rem;

    &-title,
    &-title-center {
        font-size: var(--h2-font-size);
        color: var(--title-color);
        text-align: center;
        margin-bottom: var(--mb-3);
    }
}

//======== Aletnativa a iconos =======
.scrolltop__icon .uno::before {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    margin: 5px 3px 3px 3px;
    border-top: 3px solid #ffffff;
    border-left: 3px solid #ffffff;
    border-radius: 0 5px;
    transform: rotate(45deg);
}

.nav__toggle {
    width: 20px;
    height: 20px;
    margin: 0 auto;
    border: 2px solid transparent;
    position: relative;
    align-content: center;

    & span {
        &.uno::before {
            content: "";
            display: block;
            width: 7px;
            height: 7px;
            border: 2px solid var(--title-color);
            top: 0;
            left: 0;
            position: absolute;
        }

        &.dos::before {
            content: "";
            display: block;
            width: 7px;
            height: 7px;
            border: 2px solid var(--title-color);
            top: 0;
            right: 0;
            margin: auto;
            position: absolute;
        }

        &.tres::before {
            content: "";
            display: block;
            width: 7px;
            height: 7px;
            border: 2px solid var(--title-color);
            bottom: 0;
            left: 0;
            position: absolute;
        }

        &.cuatro::before {
            content: "";
            display: block;
            width: 7px;
            height: 7px;
            border: 2px solid var(--title-color);
            bottom: 0;
            right: 0;
            position: absolute;
        }
    }
}

.bx-toggle-left {
    width: 2.5rem;
    height: 1.2rem;
    border: 2px solid var(--title-color);
    border-radius: 10px;

    &::before {
        content: "";
        display: block;
        width: 0.7rem;
        height: 0.7rem;
        background-color: #181414;
        margin: 0.07rem 0.2rem;
        left: 0;
        border-radius: 50%;
    }
}

.bx-toggle-right {
    width: 2.5rem;
    height: 1.2rem;
    border: 2px solid #ece4e4;
    border-radius: 10px;
    &::after {
        content: "";
        display: block;
        width: 0.7rem;
        height: 0.7rem;
        background-color: #ece4e4;
        margin: 0.07rem 0.2rem;
        left: 0;
        right: -10px;
        border-radius: 50%;
    }
}

.carrito__icono {
   

    & .carrito__cuadrado::before {
        content: " ";
        display: block;
        width: 20px;
        height: 15px;
        border: 2px solid var(--title-color);
        left: 0;
        right: 0;
        margin: 0 auto;
        border-radius: 0 0 30% 30%;
    }

    & .carrito__asa::before {
        content: " ";
        display: block;
        width: 10px;
        height: 8px;
        bottom: 0;
        margin: 0 auto;
        border-top: 2px solid var(--title-color);
        border-right: 2px solid var(--title-color);
        border-left: 2px solid var(--title-color);
        border-radius: 40% 40% 0 0;
    }
}




/*========== LAYOUT ==========*/
.l-main {
    overflow: hidden;
}
.bd-container {
    max-width: 968px;
    width: calc(100% - 3rem);
    margin-left: var(--mb-3);
    margin-right: var(--mb-3);
}

.bd-grid {
    display: grid;
    gap: 1.5rem;
}

.l-header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--nav-color);
}

/*========== NAV ==========*/
.nav {
    height: var(--header-height);
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: 1fr;

    // justify-content: space-between;
    text-align: center;
    align-items: center;

    &__menu {
        @media screen and (max-width: 769px) {
            position: fixed;
            top: -100%;
            left: 0;
            right: 0;
            width: 90%;
            margin: 0 auto;
            padding: 2.5rem 0 0;
            text-align: center;
            background-color: var(--nav-color);
            transition: 0.4s;
            box-shadow: 0 0 4px var(--nav-productos-shadow);
            border-radius: 2rem;
            z-index: var(--z-fixed);
        }
    }
    &__item {
        margin-bottom: var(--mb-3);
    }
    &__link,
    &__logo,
    &__toggle,
    &__product {
        color: var(--title-color);
        font-weight: var(--font-semi-bold);
    }
    &__logo {
        &:hover {
            color: var(--first-color);
        }
    }
    &__link,
    &__product {
        transition: 0.3s;

        &:hover {
            color: var(--first-color);
        }
    }
    &__toggle {
        font-size: 1.3rem;
        cursor: pointer;
        @include tabletMax {
            right: 0;
            grid-column: 3 / 4;
            grid-row: 1 / 2;
        }
    }
    &__product {
        position: relative;
    }

   

    &__usuario i{
        color: var(--title-color);
        font-size: 1.5rem;
    }
}

.carrito__icono {
    position: relative;
    font-size: 1.3rem;
    cursor: pointer;
    
    @include tabletMax {
        grid-column: 4 / 5;
    }

    & .carrito__count {
        position: absolute;
        display: block;
        width: 1.10rem;
        height: 1.10rem;
        top: 0;
        right: 0;
        bottom: 0;
        left: 1rem;
        margin: 0 auto;
        background-color: var(--first-color-send);
        border-radius: 50%;
        font-size: .8rem;
        color: var(--title-color);

        & p {
            font-weight: 600;
            font-style: normal;
            color: #ffffff;
        }
    }
}

/* Account */
.account {
    position: absolute;
}

/* Show menu */
.show-menu {
    top: calc(var(--header-height) + 1rem);
}

/* Active menu link */
.active-link {
    position: relative;

    &::before {
        content: "";
        position: absolute;
        bottom: -0.75rem;
        left: 45%;
        width: 5px;
        height: 5px;
        background-color: var(--title-color);
        border-radius: 50%;
    }
}

/* Change background header */
.scroll-header {
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

/* Scroll top */
.scrolltop {
    position: fixed;
    right: 1rem;
    bottom: -20%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.3rem;
    background: #cf4444;
    border-radius: 0.4rem;
    z-index: var(--z-tooltip);
    transition: 0.4s;
    visibility: hidden;

    &:hover {
        background-color: var(--first-color);
    }

    &__icon {
        font-size: 1.5rem;
        color: var(--body-color);
    }
}

.show-scroll {
    visibility: visible;
    bottom: 1.5rem;
}
 
pre {
    position: fixed;

    z-index: 1100;
}