.carrito {
    &__deploiment {
        display: none;
        margin: 1rem 1rem 1rem 1rem;
        text-align: center;
        position: absolute;
        top: 98%;
        left: 0;
        right: 0;
        margin: 0 auto;
        background-color: var(--body-color);
        box-shadow: 0px 13px 23px -10px var(--nav-productos-shadow);
        border-radius: 0 0 2rem 2rem;
        margin: 0 3rem;

        @include tablet {
            position: absolute;
            padding: 0.5rem 2rem 2rem 2rem;
            width: 20%;
            margin: 0 2rem;
            top: 4rem;
            left: 65%;
            text-align: center;
            transition: 0.4s;
            z-index: var(--z-fixed);
        }
    }

    &__foto {
        display: inline-block;
        width: 2rem;
        height: 2rem;
        background-size: cover;
        color: green;
    }

    &__grid {
        display: grid;
        row-gap: 1rem;

        &:first-child {
            margin-top: 1rem;
        }
    }
}

.mostrar {
    display: block;
    width: max-content;
    margin: max-content;

    &::before {
        content: "";
        display: block;
        left: 0;
        right: 0;
        margin: 0 auto;
        width: 10px;
        height: 10px;
        border-right: 3px solid var(--title-color);
        border-bottom: 3px solid var(--title-color);
        transform: rotate(45deg);
    }
}


// Pagina de Carrito

.seccion__carrito {
    padding: 1rem 0;

    h3, p {
        text-align: center;
    }
}

.contenido__carrito {
    padding: 1rem;
}

.thead__carrito {
    text-align: center;
}

.thead__carrito th {
    font-size: 1.5vh;

    
}

