.buscador {
    border-color: transparent;
    border-radius: 20px 0 0 20px;
    background-color: $gris;
}

.producto__index {
    z-index: 50;
}

.product__home {
    text-align: center;
}

.product {
    padding: 1rem;
    margin: 3rem;
    border: 1px solid #fff;
    border-radius: 1.5rem;
    box-shadow: 0 0 30px var(--nav-productos-shadow);
    max-width: 100%;
    height: 100%;

    &__item {
        position: relative;
        
    }

    &__list {
        margin: 1rem;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 1rem;
    }

    .seccion_ocultar {
        display: none;
    }

    &.total__grid {
        
        display: grid;
        grid-template-rows: 1fr;
    }
}

.accessory__items__grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    padding-bottom: 2rem;
    gap: 2rem;
    margin: 0 1.5rem;

    .revel__product {
        position: relative;
        display: grid;
        padding: .25rem .75rem .75rem;
        background-color: $blanco;
        box-shadow: 0 2px 6px rgba(65, 11, 16, .15);
        border-radius: 1rem;
    
        &:hover{
            box-shadow: 0 3px 12px rgba(65, 11, 16, .15);
        }
    }
}

.mostrar-seccion {
    display: block!important;
}

.activado {
    
    &::after {
        content: '';
        display: block;
        position: absolute;
        width: 10px;
        height: 10px;
        border-top: 2px solid var(--title-color);
        border-right: 2px solid var(--title-color);
        top: 8px;
        left: -1rem;
        transform: rotate(45deg);
    }
    
}

@include tabletChica {
    .accessory__items__grid {
        display: grid;
        grid-template-columns: repeat(2,1fr);
        padding-bottom: 2rem;
        gap: 2rem;
        margin: 0 1.5rem;
    }

    .product {
        &__grid {
            grid-template-columns: repeat(4, 1fr);
        }
    }

}

@include tablet {

    .product {

        &__grid {
            grid-template-rows: repeat(4, 1fr);
            grid-template-columns: 0;
            gap: 2rem;
        }
        &__list {
            border-right: 1px solid #ffffff;
        }
    
        &__item {
            position: relative;
        }
    }

    .total__grid {
        grid-template-columns: 25% 75%;
    }

    .accessory__items__grid {
        grid-template-columns: repeat(2,1fr);
        padding-bottom: 2rem;
        gap: 2rem;
        margin: 0 1.5rem;
    }
}



@include desktop {
    .accessory__items__grid {
        grid-template-columns: repeat(3,1fr);
        padding-bottom: 2rem;
        gap: 2rem;
        margin: 0 1.5rem;
    }

    .product {
        padding: 3rem;
    }

    .total__grid {
        grid-template-columns: 15% 85%;
    }
}

.accessory__form {
    margin: 0 1rem 1rem;
}


