.formulario {
    margin-top: 1rem;
    p {
        font-size: 1.4rem;
        text-align: center;
        color: var(--text-color);
        margin: 1rem;
    }

    legend {
        font-size: 2rem;
        text-align: center;
        color: var(--title-color);
    }

    label {
        font-weight: var(--font-bold);
        text-transform: uppercase;
        display: block;
    }
    input:not([type="submit"]), 
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid var(--text-color);
        border-radius: 1rem;
        margin-bottom: 2rem;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }
    
    input[type="submit"] {
        border: 0;
    }

}

.form {
    &__titulo {
        text-align: center;
    }

    &__grid {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    
        label {
            text-align: center;
        }
    }

    &__buscar {
        display: flex;
        align-items: center;
        justify-content: center;

        input[type="text"] {
            padding: 1rem;
            
        }

        input[type="submit"] {
            width: 6rem;
        }
    }
}

.imagen-tabla {
    width: 100px;
    
}

.imagen-small {
    width: 50px;
}

table {
    text-align: center;
}

.formato {
    margin: 1rem;
    background-color: $rojo;
}

input {

    &[value='Eliminar'] {
        background-color: $rojo;
        border: none;
        padding: .5rem;
        color: #fff;
        border-radius: 10px 10px 0 0;
        
        &:hover{
            cursor: pointer;
        }
    }
    
}

.actualizar {
    background-color: $verde;
    border: none;
    padding: .5rem;
    color: #fff;
    font-size: .8rem;
    border-radius: 0 0 10px 10px;
}

